/* Página HOME */

.demo-big-content {
    display: inline-block;
    vertical-align: middle;
    margin: 0 auto;
    width: 85%;
    padding-bottom: 30px;
    text-align: center;
    justify-content: center;
    align-items: center;
    
}

.landing-grid {

    display: inline-block;
    vertical-align: middle;
    text-align: center;
    background-size: cover !important;
    -webkit-background-size: cover !important;
    height: 100%;
    width: 100%;
}

.avatar-image {
    margin-top: 5px;
    height: 230px;
}

.cell-unique {
    height: 20%;
}

.banner-text {
    /* background-color: black; */
    /* opacity: .7; */
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: auto;
    border-radius: 10px;
}

.banner-text h1, h2 {
    font-size: 66px;
    font-weight: bold;
    color: rgb(255, 255, 255);
    text-align: center;
}
.banner-text hr {
    border-top: 5px dotted white;
    width: 85%;
    display: inline-block;
    vertical-align: middle;
    margin: 0 auto;
    width: 85%;
    padding-bottom: 30px;
    text-align: center;
}

.banner-text p {
    color: white;
    font-size: 1.8em;
    padding: 1em;
    text-align: center;
    line-height: 2em;
    
}

.social-links {
    display: flex;
    justify-content: space-evenly;
    width: 50%;
    margin: auto;
}

.social-links i {
    color: white;
    font-size: 3em;
    padding: 8px;
}

@media only screen and (min-width: 320px) and (max-width:768px){
    .landing-grid {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 5px 0 5px;
        box-sizing: border-box;
        justify-content: flex-end;
    }
    .ld-1 {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        padding-bottom: 30px;
        text-align: center;
        height: 100%;       
        box-sizing: border-box;        
        justify-content: center;
        align-items: center;
        position: relative;
    }
    .ld-1::after {
        content: attr(data-texto);
        position: absolute;
        top: 0;
        transform: translateY(-100%);
        text-align: center;
    }
    .banner-text h2 {
        font-size: 30px;
    }
    .banner-text h1 {
        margin: 50px;
        font-size: 35px;
    }
    .social-links i {
        color: white;
        font-size: 2em;
        padding: 8px;
    }
    .banner-text p {
        color: white;
        font-size: 1.3em;
        text-align: center;
    }
}

@media only screen and (min-width: 426px) {
    .banner-text h2 {
        font-size: 40px;
    }
    .banner-text h1 {
        font-size: 55px;
    }
    .landing-grid {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 5px 0 5px;
        box-sizing: border-box;
        justify-content: flex-end;
    }

    .ld-1 {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        padding-bottom: 30px;
        text-align: center;
        height: 100%;       
        box-sizing: border-box;        
        justify-content: center;
        align-items: center;
        position: relative;
    }
    .ld-1::after {
        content: attr(data-texto);
        position: absolute;
        top: 0;
        transform: translateY(-100%);
        text-align: center;
    }
}