/* Página do Curriculo CSS */

.resume-col-dir {
    background: #27221F;
    color: white;
    padding: 2em;
    text-align: left;
}

.resume-img {
    margin-top: auto;
}

@media only screen and (min-width: 220px) and (max-width:768px) {
    .resume-img {
        margin-top: 100px;        
    }
    .resume-col-dir h2 {
        font-size:  1.5em;
    }

    .resume-col-dir h2 {
        font-size:  1.5em;
    }
    resume-col-dir #teste p {
        font-size: 0.2em;
    }

}

@media only screen and (min-width: 426px) and (max-width: 1024px) {
    .resume-img {
        margin-top: 100px;
    }
    
}
