/* Página de CONTATO */

.contact-body {
	margin: auto;
	position: fixed;
	width: 100%;
	height: 100%;
	justify-content: center;
		
}

.contact-grid {
	text-align: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	background: white;
	color: black;
	border: 2px solid #29323c;
	margin-bottom: 20px;
	margin-top: auto;
}

.contact-grid h2 {
	font-family: 'Anton', sans-serif;
	color: #000;
}

.contact-grid p {
	font-family: 'Oxygen', sans-serif;
	font-weight: bold;
}

.contact-grid hr {
	border-top: 5px dotted black;
	width: 50%;
	margin: auto;
}

.lista-contato i {
	font-size: 66px;
	padding-right: 5rem;
}

.list-content {
	font-size: 25px;
	font-family: 'Anton', sans-serif;
}



@media only screen and (min-width: 220px) and (max-width:768px){
 
	/* CONTATO */
	.contact-body {
	margin: auto;
	position: relative;
	width: 100%;
	height: 100%;
	justify-content: center;
	}

	.lista-contato i {
		font-size: 20px;
		padding-right: 5rem;
	}
	.list-content {
		font-size: 10px;
		font-family: 'Anton', sans-serif;
	}
	.cell2 {
		
		width: 100%;
		height: 100%;
		position: relative;
	}
    



}

